import {Form, Divider, Input, InputNumber, Button, Upload, message} from 'antd'
import './index.css';
import { useState } from "react";
import { API_URL } from '../config/constants';
import axios from "axios";
import {useHistory} from 'react-router-dom';

function UploadPage(){
    const [imageUrl, setImageUrl] = useState(null);
    const history = useHistory();
    const onSubmit = (values) =>{
        console.log(values);
        axios
        .post(`${API_URL}/products`, {
          name: values.name,
          description: values.description,
          seller: values.seller,
          price: parseInt(values.price),
          imageUrl: imageUrl,
        })
        .then((result) => {
          console.log(result);
          history.replace('/');
        }).catch(function(error){
            console.error('',error);
            message.error(` . ${error.message}`)
        });
    }
    const onChangeImage = (info) => {
        if (info.file.status === "uploading") {
          return;
        }
        if (info.file.status === "done") {
          const response = info.file.response;
          const imageUrl = response.imageUrl;
          setImageUrl(imageUrl);
        }
      };
    return (
        <div id='upload-container'>
            <Form name=' ' onFinish={onSubmit}>
                <Form.Item name="upload" label={<div className='upload-label'> </div>}>
                   <Upload name='image' action={`${API_URL}/image`}  listType="picture" showUploadList={false} onChange={onChangeImage}>
                   {imageUrl ? (
                        <img id="v" src={`${API_URL}/${imageUrl}`} />
                        ) : (
                        <div id="upload-img-placeholder">
                            <img src="/images/icons/camera.png" />
                            <span> .</span>
                        </div>
                    )}
                    </Upload>
                </Form.Item>
                <Divider/>
                <Form.Item label={<div className='upload-label'>  </div>}  name="seller" rules={[{required: true, message:'  '}]}>
                    <Input className='upload-name' size='large' placeholder=' .'/>
                </Form.Item>
                <Divider/>
                <Form.Item name="name" label={<div className='upload-label'>   </div>} rules={[{required: true, message : '  .'}]}>
                    <Input className='upload-name' size='large' placeholder='  .'/>
                </Form.Item>
                <Divider/>
                <Form.Item name="price" label={<div className='upload-label'>   </div>} rules={[{required: true, message : '  .'}]}>
                    <InputNumber defaultValue={0} className='upload-price' size='large'/>
                </Form.Item>
                <Divider/>
                <Form.Item name="description" label={<div className='upload-label'>   </div>} rules={[{required: true, message : '  .'}]}>
                    <Input.TextArea id='product-description' size='large' placeholder='  .' showCount={true} maxLength={300}/>
                </Form.Item>
                <Form.Item>
                    <Button id='submit-button'size='large' htmlType='submit'>
                         
                    </Button>
                </Form.Item>
            </Form>
        </div>
    );
}

export default UploadPage;